@import "./default.scss";
@import './loading.scss';

.container {
  .nav-left {
    height: calc(100vh);
    color: #fff;
    // background-color: #001529;
  }

  .main {
    position: relative;
    height: calc(100vh);
    background-color: $colorL;
    overflow: auto;
  }

  .content {
    position: relative;
    padding: 20px;
  }
}

.content-wrap {
  background: #fff;
  border: 1px solid #e8e8e8;
  margin-top: -3px;

  .ant-atble-wrapper {
    margin: 0 -2px 0 -1px;
  }
}



.simple-page {
  .header-top {
    background: #1890ff;
    color: #ffffff;
  }

  .ant-form,
  .ant-col-12,
  .weather {
    display: none;
  }
}

.content {
  position: relative;
  padding: 20px;
  // background-color: $colorL;
  height: 100vh;
}